  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}

h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

/* Real CSS starts here */
:root {
  --faded-purple: rgb(52, 30, 120);
  --ivory-white: rgb(235, 235, 235);
  --light-violet: rgb(123, 58, 181);
}

* {
  /* outline: 1px solid red; */
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

html, body {
  height: 100vh;
  width: 100vw;
}

body {
  display: flex;
  position: relative;
}

#menu-sidebar {
  display: flex;
  flex-direction: column;
  width: 10%;
  background: linear-gradient(to right, var(--faded-purple), rgb(123, 58, 181));
  box-shadow: 5px 0 5px 5px rgb(123, 58, 181);
  z-index: 1;
  height: 100vh;
  display: block;
  position: fixed;
}

#menu-sidebar button {
  appearance: none;
  border: none;
  outline: none;
  background: none;
  color: var(--ivory-white);
  font-weight: 700;
  margin: 3px;
  transition: all 0.3s ease-in-out;
  border-radius: 2px;
  width: 100%;
  cursor: pointer;
}

#menu-sidebar button:hover {
  background-color: var(--ivory-white);
  color: var(--faded-purple);
  box-shadow: 0px 0px 4px 2px var(--ivory-white);
}

#main-body {
  width: 90%;
  margin-left: 10%;
}


#top {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 5px;
  background-color: rgba(70, 13, 70, 0.418);
  box-shadow: 0px 0px 10px 10px rgb(70, 13, 70, 0.418);
  outline: none;
  border: none;
  z-index: 0;
}

#top>* {
  width: 50vw;
  margin: 5px;
  border-radius: 10px;
  position: relative;
  padding: 10px;
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: height 0.3s ease;
}



/* Collapsed (default) */
#top>* {
  height: 10vh;
  overflow: hidden;
}

/* Expanded */
#top>*.expanded {
  height: 50vh;
  overflow-y: auto;
}

/* Blur at bottom only in collapsed mode */
#top>*::after {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  height: 30px;
  width: 100%;
  pointer-events: none;
  background: linear-gradient(to bottom, transparent, rgba(255, 255, 255, 0.9));
}



#late::before {
  content: "Late Tasks";
  font-weight: bold;
  padding-left: 5px;
  padding-bottom: 10px;
}

#soon::before {
  content: "Upcoming Tasks";
  font-weight: bold;
  padding-left: 5px;
  padding-bottom: 10px;
}




#content {
  display: flex;
}

#group-nav {
  width: 20%;
  margin-top: 20px;
}

#group-nav>* {
  display: flex;
  /* flex-direction: column; */
  justify-content: space-between;
  align-items: center;
  height: 3rem;
  background: linear-gradient(to right, var(--light-violet), rgb(177, 130, 238));
  box-shadow: 0px 0px 5px 5px var(--light-violet);
  padding-left: 25px;
  color: var(--ivory-white);
  font-weight: 700;
  border-radius: 10px;
  transition: all 0.3s ease-in-out;
  margin-bottom: 10px;
}

#group-nav>*:hover {
  background: none;
  background-color: rgb(197, 152, 211);
  color: var(--faded-purple);
  box-shadow: 0px 0px 4px 2px rgb(162, 130, 238);
  padding-left: 20%;
}

.group-entry>span {
  width: 100%;
  height: 100%;
}

.group-entry, .group-entry>* {
  cursor: pointer;
}

#group-nav button {
  appearance: none;
  border: none;
  outline: none;
  background: none;
  color: var(--faded-purple);
  font-weight: 700;
  font-size: 0.8rem;
  margin: 3px;
  transition: all 0.3s ease-in-out;
  border-radius: 2px;
  cursor: pointer;
}

#group-nav button:hover {
  background-color: var(--ivory-white);
  color: var(--faded-purple);
  box-shadow: 0px 0px 4px 2px var(--ivory-white);
}

#task-group {
  width: 80%;
  padding: 10px;
  padding-left: 10px;
}

#task-group>* {
  margin: 10px;
  padding: 20px;
  border: 3px dashed var(--faded-purple);
  border-radius: 5px;
}

.task {
  margin: 10px;
  padding: 10px;
}

#soon, #late {
  cursor: pointer;
  padding: 0.5rem;
  background-color: #eee;
  /* font-weight: bold; */
  position: sticky;
  top: 0;
  z-index: 10;
}

#soon.expanded>.task,
#late.expanded>.task {
  display: block;
}

/* dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1);
} */

dialog {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  opacity: 0;
  transition:
    opacity 0.3s ease-out,
    transform 0.3s ease-out,
    overlay 0.3s ease-out allow-discrete,
    display 0.3s ease-out allow-discrete;

  /* width: 35vw; */
  border: none;
  outline: none;
  border-radius: 1rem;
  box-shadow: 4px 4px 5px rgba(75, 75, 75, 0.589);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

/* When open, animate in */
dialog:open {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

/* Ensure it *starts* from this style when opening */
@starting-style {
  dialog:open {
    opacity: 0;
    transform: translate(-50%, -50%) scale(0);
  }
}

/* Backdrop fade */
dialog::backdrop {
  background-color: rgb(0 0 0 / 0%);
  transition:
    display 0.3s allow-discrete,
    overlay 0.3s allow-discrete,
    background-color 0.3s;
}

dialog:open::backdrop {
  background-color: rgb(0 0 0 / 25%);
}

@starting-style {
  dialog:open::backdrop {
    background-color: rgb(0 0 0 / 0%);
  }
}

dialog>* {
  justify-items: center;
  margin: 10px;
}

form {
  display: flex;
  flex-direction: column;
  margin: 10px;
}

form>* {
  padding: 10px;
}

form input, textarea, select {
  border: 2px solid var(--faded-purple);
  outline: none;
  /* line-height: 0.8rem; */
  padding: 0.2rem;
  font-size: 1rem;
}

textarea {
  resize: none;
